@import url("./bootstrap-reboot.css");
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap');

:root {
    --black: #262f3d;
    --gray: #7b8ba4;
    --teal: #abc4ff;
    --darkteal: #265cdb;
    --lightblue: #d5e1f7;
    --white: #fff;
    --yellow: #ffdb54;
}

html {
    font-size: 16px;
    scroll-behavior: smooth;
}

body {
    font-family: "Work Sans", sans-serif;
    font-weight: 400;
    line-height: 1.6;
    margin: 0;
    padding: 0;
    width: 100vw;
    color: var(--black);
    overflow-x: hidden;
    background: var(--lightblue);
}

body.scrollingDisabled {
    overflow: hidden;
}

.image-modal {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: scroll;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.2);
    z-index: 999;
    display: none;
    opacity: 0;
    place-items: center;
}

.image-modal-inner {
    width: calc(100% - 6rem);
    height: 0;
    margin: 3rem;
    padding-bottom: calc((100% - 6rem) * 0.66125);
    position: relative;
    background: #222;
    overflow: hidden;
    border-radius: 0.5rem;
    box-shadow: 0 2rem 2rem -0.5rem rgba(0, 0, 0, 0.5);
}

.image-modal-inner img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.modal-close {
    position: absolute;
    z-index: 9999;
    top: 0;
    right: 3rem;
    background: var(--black);
    color: var(--white);
    box-shadow: 0 0.5rem 0.5rem rgba(45, 147, 173, 0.15);
    padding: 0.33rem 0.66rem;
    border-radius: 0 0 0.5rem 0.5rem;
    cursor: pointer;
    user-select: none;
}

@media (min-width: 1150px) {
    .image-modal-inner {
        width: 1024px;
        margin: 0;
        padding-bottom: 678px;
    }
}

@media (max-width: 550px) {
    .image-modal {
        display: none !important;
    }
}

.view-more {
    display: flex;
}

.designs .view-more {
    margin: 3rem auto 0 auto;
}

.view-more .button {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.view-more .button svg {
    margin-right: 0.5rem;
}

.wrapper {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
}

.container {
    width: 100%;
    padding: 8rem 5rem 5rem 5rem;
    overflow: hidden;

    /* opacity: 0; //for preloader */
}

::selection {
    background: var(--teal);
    color: var(--white);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
}

h2 {
    font-family: 'Poppins', sans-serif;
    letter-spacing: 0.2rem;

}

.heading {
    font-weight: 700;
    position: relative;
    margin-bottom: 3rem;
    color: var(--teal);
    z-index: 2;
    text-transform: uppercase;
    white-space: nowrap;
}

.heading::before {
    position: absolute;
    content: "";
    height: 3rem;
    width: 3rem;
    border-radius: 99px;
    background: var(--lightblue);
    z-index: -1;
    left: -1.5rem;
    top: -0.5rem;
    box-shadow: -2px 2px var(--white), -4px 4px var(--teal);
}

.heading.white::before {
    background: var(--white);
    box-shadow: -2px 2px var(--lightblue), -4px 4px var(--teal);
}

img {
    width: 100%;
}

.mobile,
.hero-form.mobile,
.nav-social.mobile,
.nav-info.mobile {
    display: none;
}

header {
    background: var(--lightblue);
}

header .container {
    padding: 2rem 5rem;
}

header nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header nav #navLogo {
    height: 3rem;
    margin-left: -0.25rem;
    fill: var(--black);
    width: auto;
}

.navbar {
    list-style-type: none;
    display: flex;
    margin: 0;
    align-items: center;
}

.navbar li+li {
    margin-left: 1rem;
}

.nav-link {
    color: var(--black);
    position: relative;
    transition: color 0.3s;
    padding: 0.5rem 1rem;
    display: inline-block;
}

.nav-link.last {
    margin-left: 1rem;
    background: var(--darkteal);
    color: var(--white);
    border-radius: 99px;
}

.nav-link:hover {
    color: var(--teal);
}

.nav-link.last:hover {
    color: var(--white);
}

.nav-link::before {
    position: absolute;
    content: "";
    width: 0;
    height: 1px;
    border-radius: 2px;
    background: var(--teal);
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    transition: width 0.3s;
}

.nav-link:hover::before {
    width: 50%;
}

.nav-info {
    display: flex;
    align-items: flex-end;
    margin-top: auto !important;
    align-self: center;
    color: var(--gray);
}

.nav-info span {
    margin-left: 0.5rem;
}

.nav-info svg {
    height: 3.25rem;
    width: auto;
    transform: scale(0.85);
    fill: var(--gray);
}

.nav-info h3 {
    text-transform: uppercase;
    margin: 0;
    font-weight: 800;
}

.nav-info p {
    margin: 0;
    line-height: 1.2;
}

.nav-social {
    display: flex;
    align-self: center;
}

.nav-social a {
    display: block;
    font-size: 1.5rem;
    margin-top: 1rem;
    position: relative;
    z-index: 2;
}

.nav-social a+a {
    margin-left: 1rem;
}

.nav-social a span::before,
.nav-social a span {
    color: var(--gray);
    transition: all 0.2s;
}

.nav-social a:hover span::before {
    color: var(--teal);
}

.nav-social a:hover span {
    transform: scale(1.15);
}



section#hero {
    background: var(--lightblue);
    position: relative;
}

.hero-inner {
    display: flex;
    padding: 5rem 3rem 5rem 5rem;
    align-items: center;
}

.hero-inner .container {
    padding: 0;
    overflow: visible;
}

.container.split:nth-child(1) {
    flex: 0.65;
    margin: 5rem 0;
    width: fit-content;
}

.container.split:nth-child(2) {
    flex: 0.35;
    padding: 3rem;
    margin-top: 0;
}

.container.split:nth-child(2) svg {
    width: 100%;
}

.scrollDown {
    position: absolute;
    right: 3rem;
    bottom: 5rem;
    overflow: hidden;
    box-shadow: 0 0 0 1.5px var(--black);
    border-radius: 99px;
    display: flex;
    justify-content: center;
    width: 1.5rem;
    height: 3rem;
}

.scrollDown::before {
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    box-shadow: 0 0 0 1.5px var(--black);
    content: "";
    top: 0.5rem;
    animation: scrollDown 2s infinite linear;
    border-radius: 99px;
}

@keyframes scrollDown {
    0% {
        top: 0.5rem;
        height: 0.5rem;
        opacity: 0;
    }

    25%,
    40% {
        top: 0.5rem;
        height: 0.5rem;
        opacity: 1;
    }

    50% {
        height: 1.5rem;
        top: 1rem;
        opacity: 1;
    }

    60%,
    75% {
        height: 0.5rem;
        top: 2rem;
        opacity: 1;
    }

    100% {
        height: 0.5rem;
        top: 2rem;
        opacity: 0;
    }
}

h1.hero-title {
    font-weight: 900;
    -webkit-text-stroke: 2px var(--black);
    color: var(--lightblue);
    font-size: 3.25rem;
    text-shadow: -0.25rem 0.25rem var(--black);
    font-family: "Montserrat", sans-serif;
    z-index: 1;
    position: relative;
    width: fit-content;
    user-select: none;
    margin: 0;
    margin-bottom: 5rem;
}

.hero-title .highlight {
    position: relative;
    z-index: 1;
    white-space: nowrap;
    display: inline-block;
}

span.highlight svg {
    position: absolute;
    height: 1.5rem;
    width: 100%;
    fill: var(--yellow);
    bottom: -1.5rem;
    right: 0;
    z-index: -1;
}

.hero-title .title-top {
    text-shadow: none !important;
    -webkit-text-stroke: 0 !important;
    display: block;
    margin-bottom: 1rem;
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase;
    line-height: 1;
    color: var(--teal) !important;
    letter-spacing: 5px;
    transform: scaleY(0.85);
}

.hero-form {
    display: flex;
    width: 75%;
    max-width: 600px;
}

.hero-form .email {
    flex: 1;
    border-radius: 0.5rem 0 0 0.5rem;
    border: none;
    background: var(--lightblue);
    box-shadow: inset 0 0 0 1.5px var(--teal);
    padding: 0.75rem 1rem;
    font-weight: 500;
    transition: all 0.2s;
}

.hero-form .email:focus {
    outline: none;
    box-shadow: inset 0 0 0 1.5px var(--teal),
        0 0.25rem 0.55rem rgba(45, 147, 173, 0.2);
}

.email::placeholder {
    color: var(--gray);
    font-weight: 500;
}

.hero-form .send button {
    border-radius: 0 0.5rem 0.5rem 0;
}

@media (max-width: 850px) {
    h1.hero-title {
        font-size: 3rem;
    }

    .hero-title br {
        display: none;
    }

    .scrollDown {
        display: none;
    }

    .hero-inner {
        padding: 0;
    }

    .container.split.one {
        flex: 1;
        width: 100%;
    }

    .container.split.two {
        display: none;
    }
}

.cool {
    font-size: 1rem;
    color: var(--teal);
    font-weight: 500;
    margin-bottom: 0.25rem;
    display: block !important;
}

@media (max-width: 550px) {
    #hero .container {
        margin: 0;
        margin-bottom: 5rem;
    }

    .hero-title .title-top {
        white-space: nowrap;
        letter-spacing: 0 !important;
        width: fit-content;
        position: relative;
        display: block;
        font-size: 1.75rem;
    }

    .title-top i {
        position: absolute;
        display: block;
        width: 25%;
        padding-bottom: 25%;
        transform: scaleY(1.15);
        border-radius: 50%;
        background: var(--yellow);
        right: -5%;
        top: -67%;
        z-index: 2;
        mix-blend-mode: color-dodge;
        animation: blob 10s infinite linear, blobMotion 15s infinite alternate;
    }

    @keyframes blobMotion {

        0%,
        100% {
            transform: scale(1, 1.15);
        }

        50% {
            transform: scale(1.15, 1.3) translateX(-1.3rem) rotate(10deg);
        }
    }

    @keyframes blob {

        0%,
        100% {
            border-radius: 33% 67% 70% 30% / 30% 30% 70% 70%;
        }

        20% {
            border-radius: 37% 63% 51% 49% / 37% 65% 35% 63%;
        }

        40% {
            border-radius: 36% 64% 64% 36% / 64% 48% 52% 36%;
        }

        60% {
            border-radius: 37% 63% 51% 49% / 30% 30% 70% 70%;
        }

        80% {
            border-radius: 40% 60% 42% 58% / 41% 51% 49% 59%;
        }
    }

    h1.hero-title {
        font-size: 2.25rem;
        font-size: clamp(2rem, 10vw, 2.5rem);
        text-shadow: -0.15rem 0.15rem var(--black);
        line-height: 1.35;
        margin-bottom: 3.5rem;
    }

    .hero-title b {
        font-size: 0.9rem;
        margin-top: 1rem;
    }

    .hero-form.mobile {
        display: flex;
        width: 100%;
        margin: 0;
    }

    .hero-form.desktop {
        display: none !important;
    }

    .hero-form .email {
        flex: calc(100% - 3rem);
        width: calc(100% - 3rem);
    }

    .hero-form .send button {
        width: 3rem;
        padding: 0;
    }
}

.about-who .hero-image {
    width: 35%;
    max-width: 300px;
    position: relative;
    z-index: 2;
    margin-bottom: auto;
}

img.avatar {
    filter: drop-shadow(0 -0.5rem 1rem rgba(0, 0, 0, 0.15));
}

img.avatar-bg {
    position: absolute;
    z-index: -1;
}

.about-who .hero-content {
    margin: auto 0 1rem 3rem;
    width: fit-content;
}

.hero-content h4 {
    font-weight: 800;
    color: var(--gray);
    opacity: 0.5;
    margin-bottom: 0;
    position: relative;
    display: inline;
}

.hero-content .title {
    font-weight: 800;
    margin-bottom: 1rem;
    margin-left: -0.15rem;
    line-height: 1;
    font-size: 2.5rem;
}

.phonetics {
    margin-bottom: 0.5rem;
    font-size: 1rem;
}

.phonetics .speaker {
    height: 1.25em;
    margin-bottom: 0.25rem;
    cursor: pointer;
    transform-origin: 0 75%;
}

.phonetics .speaker.isPlaying {
    filter: drop-shadow(-0.25rem -0.25rem 0.5rem rgba(0, 0, 0, 0.25));
}

.speaker .waves {
    opacity: 0;
}

.speaker.isPlaying {
    animation: speaker 0.6s infinite linear;
}

.speaker.isPlaying .waves {
    animation: wave 0.3s infinite ease alternate;
}

.waves:nth-of-type(1) {
    animation-delay: 0.15s;
}

.waves:nth-of-type(2) {
    animation-delay: 0.3s;
}

@keyframes wave {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes speaker {

    0%,
    33%,
    66%,
    100% {
        transform: scale(1);
    }

    25%,
    50% {
        transform: scale(1.2) rotate(-5deg);
    }
}

.description {
    margin-bottom: 3rem;
    max-width: 520px;
    font-size: 1rem;
}

.lighter {
    color: var(--gray);
}

a.lighter:hover {
    color: var(--teal);
}

.description b {
    font-weight: 600;
    font-family: "Work Sans", sans-serif;
}

.cta-box {
    padding: 2rem;
    margin-top: -2rem;
    margin-left: -2rem;
    margin-bottom: -2rem;
    width: fit-content;
    position: relative;
    touch-action: none;
    z-index: 2;
}

.cta {
    background: var(--lightblue);
    padding: 0.75rem 2rem 0.75rem 3.25rem;
    font-size: 1.15rem;
    border-radius: 5rem;
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    display: inline-block;
    transition: all 0.3s;
    transform-style: preserve-3d;
    backface-visibility: hidden;
    color: var(--teal);
    position: relative;
}

.cta:hover {
    color: var(--teal);
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.cta #hand {
    width: 3.25rem;
    position: absolute;
    left: -1.25rem;
    top: -0.5rem;
    animation: wavingHands 2s infinite ease-in-out;
    transform-origin: 60% 100%;
    filter: drop-shadow(0.25rem 0.25rem 0.5rem rgba(45, 147, 173, 0.2));
}

@keyframes wavingHands {

    0%,
    60%,
    100% {
        transform: rotate(0deg);
    }

    10%,
    30% {
        transform: rotate(14deg);
    }

    20% {
        transform: rotate(-8deg);
    }

    40% {
        transform: rotate(-4deg);
    }

    50% {
        transform: rotate(10deg);
    }
}

@media (max-width: 1150px) {
    .description {
        margin-bottom: 2rem;
    }

    .cta {
        padding: 0.66rem 1.5rem 0.66rem 2.75rem;
        font-size: 1.05rem;
    }

    .cta #hand {
        width: 3rem;
    }

    .about-who .hero-content {
        margin: auto 0 1rem 3rem;
        width: calc(65% - 3rem);
    }
}

@media (max-width: 850px) {
    .container {
        padding: 5rem 3rem;
    }

    .hero-inner .container {
        padding: 0 3rem 1rem 3rem;
    }

    .hero-content .title {
        font-size: 2rem;
    }

    header .container {
        padding: 3rem;
    }
}

@media (max-width: 550px) {
    .hero-inner .container {
        padding: 3rem 2rem;
    }

    .about-who .hero-image {
        display: block;
        width: 90%;
        margin: 0 auto;
    }

    .about-who .hero-content {
        margin: 0;
        margin-top: 3rem;
        width: 100%;
    }

    .hero-content .title {
        line-height: 1;
    }

    .cta {
        padding: 0.75rem 3rem;
        text-align: center;
    }

    .cta-box {
        left: 50%;
        transform: translate(-50%);
        margin-left: 0;
    }
}

@media (max-width: 450px) {

    .container,
    .hero-inner .container {
        padding: 5rem 2rem;
    }

    header .container {
        padding: 2rem;
    }

    header nav #navLogo {
        height: 2.5rem;
    }

    .navbar {
        position: fixed;
        flex-flow: column nowrap;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: var(--lightblue);
        z-index: 99;
        padding: 5rem 2rem 3rem;
        transform: translateY(-100%);
        opacity: 0;
        pointer-events: none;
        transition: transform 0.5s cubic-bezier(0.96, 0.06, 0.09, 0.98),
            opacity 0.1s 0.5s;
    }

    .navbar.open {
        transform: translateY(0);
        pointer-events: auto;
        transition: transform 0.5s, opacity 0.1s;
        opacity: 1;
    }

    .navbar::before,
    .navbar::after {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: transform 0.5s;
    }

    .navbar::after {
        z-index: -1;
        background: var(--black);
        transition-timing-function: cubic-bezier(0.3, 0.13, 0.14, 0.6);
    }

    .navbar::before {
        z-index: 99;
        background: var(--teal);
        box-shadow: inset 0 2.5rem 1rem -1.5rem rgba(0, 0, 0, 0.2),
            0 0.5rem 1rem rgba(0, 0, 0, 0.2);
        transition-timing-function: cubic-bezier(0.05, 0.18, 0.14, 0.98);
    }

    .navbar.open::after {
        transform: translateY(100%);
        transition-timing-function: cubic-bezier(0.06, 0.86, 0.49, 1);
    }

    .navbar.open::before {
        transform: translateY(100%);
        transition-timing-function: cubic-bezier(0.79, 0.06, 0.83, 0.67);
    }

    .navbar li+li {
        margin: 0;
    }

    .nav-link {
        color: var(--gray);
        margin: 0;
        font-size: 1.5rem;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        padding: 0.5rem 1rem;
    }

    .nav-link.last {
        margin-left: 0;
        background: none;
        color: var(--white);
    }

    .nav-link.last:hover {
        color: var(--teal);
    }

    .navbar li:last-of-type {
        margin-bottom: 1rem;
    }

    .nav-link::before {
        bottom: 0.25rem;
        left: 1rem;
        transform: none;
    }

    .nav-link:hover::before {
        width: 50%;
    }

    .mobile {
        display: block;
    }

    .nav-social.mobile,
    .nav-info.mobile {
        display: flex;
        align-items: center;
    }

    .nav-toggle {
        width: 2rem;
        height: 2rem;
        z-index: 999;
        position: fixed;
        right: 2rem;
        top: 2rem;
        border-radius: 99px;
    }

    .nav-toggle::before,
    .nav-toggle::after {
        position: absolute;
        content: "";
        height: 0.5rem;
        background: var(--black);
        border-radius: 99px;
        transform: scaleY(0.85);
        transition: all 0.2s;
    }

    .nav-toggle::before {
        width: 1.75rem;
        top: 0.375rem;
        right: 0.125rem;
        border-radius: 50% 50% 0 0;
    }

    .nav-toggle::after {
        width: 1.25rem;
        top: 1.125rem;
        right: 0.125rem;
        border-radius: 0 0 50% 50%;
    }

    .nav-toggle.open::after,
    .nav-toggle.open::before {
        top: 50%;
        right: 50%;
        width: 1.5rem;
        height: 0.25rem;
        border-radius: 99px;
        background: var(--gray);
    }

    .nav-toggle.open::after {
        transform: translate(50%, -50%) rotate(-45deg);
    }

    .nav-toggle.open::before {
        transform: translate(50%, -50%) rotate(45deg);
    }
}

#navLogo,
#ds,
#eye1,
#eye2 {
    transition: transform 0.3s ease-out;
}

#eye1,
#eye2 {
    transform-origin: center center;
}

#navLogo:hover {
    transform: rotate(-90deg) scaleX(0.8) scaleY(1.1);
}

#navLogo:hover #ds {
    transform: scaleY(1.05) translateY(-6%);
}

#navLogo:hover #eye1 {
    transform: scale(1.3, 1.1) translate(11%, 6%);
    transform-origin: 50% 50%;
}

#navLogo:hover #eye2 {
    transform: scale(1.3, 1.1) translate(-11%, 6%);
    transform-origin: 50% 50%;
}

.about-who {
    display: flex;
    justify-content: center;
    margin-bottom: 10rem;
}

.about-quote>h4 {
    margin: 1rem 0 5rem 1.25rem;
    font-weight: 700;
}

.quote h4 {
    font-weight: 800;
    line-height: 1.33;
    white-space: nowrap;
    color: var(--gray);
}

.quote {
    position: relative;
    z-index: 2;
    width: fit-content;
    display: flex;
    flex-flow: column nowrap;
    padding: 2rem;
    background: var(--lightblue);
    border-radius: 0.5rem;
    box-shadow: 0 0.25rem 1rem -0.25rem rgba(0, 0, 0, 0.05);
}

.quote span.fas {
    position: absolute;
    font-size: 5rem;
